@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
.title {
  display: flex;
  justify-content: center;
  padding: 2%;
  font-size: 3em;
  color: white;
  background: #00046b; }

body > div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: -webkit-fill-available; }

.repos {
  flex-grow: 1;
  display: grid;
  grid-template-columns: repeat(1, auto);
  padding: 2%;
  background: #f7f7f7;
  border-bottom: 2px solid #767676;
  transition: all .2s ease-in-out; }
  @media only screen and (min-width: 550px) {
    .repos {
      grid-template-columns: repeat(2, auto); } }
  @media only screen and (min-width: 1080px) {
    .repos {
      grid-template-columns: repeat(3, auto); } }
  .repos:hover {
    transform: scale(1.1); }

.repo {
  max-width: 400px;
  display: flex;
  flex-direction: column;
  margin: 5%;
  padding: 3%;
  border-radius: 5px;
  box-shadow: 0 5px 10px -2px rgba(116, 128, 143, 0.2);
  background: white; }

.name {
  font-size: 2em;
  color: black;
  padding-left: 0.5em; }

.name_link {
  text-decoration: none; }

.middle {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 1em; }

.bottom {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.property {
  padding-top: 1em;
  padding-bottom: 1em; }

.description {
  max-width: 240px; }

body {
  font-family: 'Roboto';
  margin: 0; }

.repos {
  color: #0c6cce; }
  .repos a:visited, .repos a:link {
    color: #0c6cce; }
  .repos a:hover {
    color: #cecece; }
