@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
.title {
  display: flex;
  justify-content: center;
  padding: 2%;
  font-size: 3em;
  color: white;
  background: #00046b; }

body > div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: -webkit-fill-available; }

.about {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #00046b; }

body {
  font-family: 'Roboto';
  margin: 0; }

.about {
  color: #ffffff; }
  .about a:visited, .about a:link {
    color: #ffffff; }
  .about a:hover {
    color: #cecece; }
